.mainCardContainer {
    margin-bottom: 50px;
    padding: 0; 
    background-color: rgb(81, 141, 81);
}

.gameMessage {
    text-align: center;
    max-width: 100%;
    -ms-flex-line-pack: center;
        align-content: center
}

.gameMessage p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:grey;
    color: white;
    padding: 20px 0;
    margin-bottom: 0px;
}

.gameScores {
    text-align: center;
    max-width: 100%;
    -ms-flex-line-pack: center;
        align-content: center;
    margin-bottom: 10px;
}
.gameScores p {
    font-family: 'Montserrat', sans-serif;
    text-transform: uppercase;
    font-weight: 500;
    background-color:green;
    color: white;
    padding: 20px 0;
    margin-bottom: 25px;
}



